import { Component } from './';
import { AutoGEOLocation, isAllowedGEOLocation } from '../utils';

class AutoLocation extends Component {
    constructor(props) {
        super(props);
        this.el = document.createElement('div');
        this.isAllow = isAllowedGEOLocation();
        this.locationObj = null;

        let l = AutoGEOLocation();

        l.then((locationObj) => {
            this.isAllow = true;
            this.locationObj = locationObj;
            var elDiv = document.createElement('div');
            elDiv.innerHTML = `lat: ${locationObj.lat}; long: ${locationObj.long};`;
            // locationObj.lat
            // locationObj.long
            this.el.appendChild(elDiv);

            console.log(locationObj);

            this.update();
        }, (err) => {
            // err

            console.log(err);

            this.update();
        })

    }

    handlerClick() {

    }

    render() {

        let html = `<div>AutoLocation Button = ${this.isAllow ? "YES" : "NO"} </div>`;

        if (this.locationObj) {
            html += `<div> lat: ${this.locationObj.lat}; long: ${this.locationObj.long}; </div>`;
        }

        return html;
    }

}

export default AutoLocation;



