import { Component, AutoLocation } from "./components"


class App extends Component {
    constructor(props) {
        super(props);
        this.el = props.el || document.createElement('div');

        this.autoLocation = new AutoLocation();

    }

    render() {

        // return `<div>DKKDKDDKDKDK</div>`;

        return [
            this.autoLocation.update()
        ]
    }
}

// export default App

var w = new App({ el: document.getElementById("root") });
w.update();



